@import url(https://fonts.googleapis.com/css?family=Dosis|PT+Sans+Narrow|Roboto+Slab:400,900);
body,
html {
  min-height: 100%;
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ebf0f5; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }


.sheet {
  max-width: 1000px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  margin: 20px auto;
  padding: 20px 10px;
  background-image: url(/portfolio/static/media/back.8bc860df.png);
  background-size: contain;
  background-repeat: no-repeat;
  height: 1000px;
  border-radius: 5px;
  box-shadow: 0px 1px 10px 1px #aaa; }

.section-header {
  margin-top: 0;
  margin-bottom: 10px;
  padding-bottom: 15px;
  position: relative; }
  .section-header:after {
    content: "";
    margin-top: 5px;
    height: 1px;
    position: absolute;
    width: 100%;
    background: -webkit-gradient(linear, 0 0, 100% 0, from(#fff), to(#fff), color-stop(50%, #999)); }
  .section-header .header-item {
    margin-bottom: 10px; }
    .section-header .header-item .header-item--thumb {
      position: relative;
      text-align: center; }
      .section-header .header-item .header-item--thumb img {
        max-width: 100px; }
    .section-header .header-item .header-item--content {
      padding-left: 20px;
      font-family: "PT Sans Narrow", sans-serif !important; }
      .section-header .header-item .header-item--content .header-item--name {
        margin: 0;
        text-transform: uppercase;
        font-size: 48px;
        font-family: "Roboto Slab", sans-serif !important; }
        .section-header .header-item .header-item--content .header-item--name .first-name {
          color: #363636;
          font-weight: 900; }
        .section-header .header-item .header-item--content .header-item--name .last-name {
          color: #747575;
          font-weight: 400; }
      .section-header .header-item .header-item--content .header-item--title {
        margin: 0;
        text-transform: uppercase;
        font-size: 24px;
        letter-spacing: 2px;
        font-weight: 500;
        color: #4e4e4e;
        font-family: "Roboto Slab", sans-serif !important; }
      .section-header .header-item .header-item--content .header-item--info {
        margin-top: 20px; }
        .section-header .header-item .header-item--content .header-item--info .header-item--info-item {
          margin: 0;
          margin-bottom: 8px;
          color: #676767;
          font-size: 18px; }
          .section-header .header-item .header-item--content .header-item--info .header-item--info-item .icon {
            padding-right: 10px;
            color: #008080; }

.experience .experience-item {
  margin-bottom: 10px; }
  .experience .experience-item .experience-item--logo {
    width: 64px;
    height: 64px;
    position: relative;
    padding: 5px; }
    .experience .experience-item .experience-item--logo img {
      max-width: 100%;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 10px;
      margin: auto;
      -webkit-filter: grayscale(100%);
              filter: grayscale(100%);
      width: 80%; }
  .experience .experience-item .experience-item--content {
    padding-left: 10px;
    font-family: "Dosis", sans-serif; }
    .experience .experience-item .experience-item--content .experience-item--employer > p {
      margin: 0; }
    .experience .experience-item .experience-item--content .experience-item--employer .employer-name {
      font-weight: 700; }
    .experience .experience-item .experience-item--content .experience-item--employer .employer-location {
      font-size: 90%;
      font-style: italic;
      color: #555; }
    .experience .experience-item .experience-item--content .experience-item--employer-duration {
      margin-top: 5px;
      color: #333; }
      .experience .experience-item .experience-item--content .experience-item--employer-duration > p {
        margin: 0; }
  .experience .experience-item .experience-item--description {
    margin: 0;
    font-size: 18px;
    font-weight: 300;
    font-family: "PT Sans Narrow", sans-serif; }

.card {
  background-color: #fff;
  border: 1px solid #ddd;
  margin-bottom: 15px;
  box-shadow: 0px 2px 4px -1px #aaa; }
  .card > .card-title h3 {
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 28px;
    color: #008080;
    text-transform: uppercase;
    font-family: "Dosis", sans-serif;
    padding: 8px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ddd;
    letter-spacing: 1px; }
  .card > .card-content {
    padding: 0; }
  .card.card-padding .card-content {
    padding: 8px; }

.education p {
  margin: 0;
  font-family: "Dosis", sans-serif; }

.education .education-item {
  margin-bottom: 10px; }
  .education .education-item .education-item--title {
    font-size: 18px;
    font-weight: 900;
    margin: 0;
    padding-bottom: 2px; }
  .education .education-item .education-item--institute {
    font-size: 16px;
    font-weight: 500;
    margin: 0;
    padding-bottom: 5px; }
  .education .education-item .education-item--details {
    font-size: 14px;
