    .education .education-item .education-item--details .education-item--location {
      text-align: right; }
  .education .education-item .education-item--score {
    margin: 3px 0;
    font-family: "Roboto", sans-serif !important;
    font-size: 15px; }
    .education .education-item .education-item--score .icon {
      padding-right: 10px;
      color: #008080; }
  .education .education-item .education-item--achievements .education-item--achievement {
    margin: 3px 0;
    font-family: "Roboto", sans-serif !important;
    font-size: 15px; }
    .education .education-item .education-item--achievements .education-item--achievement .icon {
      padding-right: 10px;
      color: #008080; }
  .education .education-item:first-child {
    margin-top: 10px; }

.section-skills .skill-items {
  margin-top: 20px; }
  .section-skills .skill-items .skill-item {
    margin-bottom: 20px; }
    .section-skills .skill-items .skill-item .skill-item--group {
      font-family: "Roboto Slab", sans-serif !important; }
      .section-skills .skill-items .skill-item .skill-item--group .skill-item--category {
        margin: 0;
        font-weight: 900;
        font-size: 16px; }
      .section-skills .skill-items .skill-item .skill-item--group .skill-item--description {
        margin: 0;
        margin-top: 3px;
