.sheet {
  max-width: 1000px;

  background-color: #f9f9f9;
  border: 1px solid #ddd;

  margin: 20px auto;
  padding: 20px 10px;

  background-image: url("./back.png");
  background-size: contain;
  background-repeat: no-repeat;
  height: 1000px;

  border-radius: 5px;
  box-shadow: 0px 1px 10px 1px #aaa;
}
